<template>
    <div class="home">
        <h1>{{ test }}</h1>
        <button @click="jump">返回首页</button>
    </div>
</template>
<script type="text/babel">
    export default {
        name: 'Index',
        data () {
            return {
                test: 'Hello World!'
            }
        },
        methods: {
            jump () {
                this.$router.push({
                    name: 'test'
                })
            }
        }
    }
</script>
<style lang="scss" type="text/scss" scoped>
    .home {

        h1 {
            font-size: 50px;
            text-align: center;
        }
    }
</style>



